function donothing() {  }

export default class DrawBox extends Component {
    constructor(props) {
        super(props);

    }

render() { 



//console.log(this.props.rowpath,this.props.mykey) 
let composition = this.props.rowpath+this.props.mykey.toString();
//console.log(composition)
var myImage = images[composition];
 
    return (   

    <TouchableOpacity onPress={
        (this.props.mydetails=='live' ) ?this.props.onPress : this.props.onPressFail  } >

    { this.props.mydetails=='live' ? 
          <View style={[styles.letterbox_live, {backgroundColor: this.props.mycolor }]} key={this.props.mykey} >
          <Text style={styles.white} key={this.props.mykey}>{this.props.mystring}</Text> 
          </View>
        : donothing() }
    {this.props.mydetails=='selected'?
        <View style={styles.letterbox_selected} key={this.props.mykey} >
            <Image style={styles.stretch} source={myImage} />
        </View> 
        : donothing() }
    { this.props.mydetails=='skipped' ? 
        <View style={styles.letterbox_skipped} key={this.props.mykey} >
        <Text style={styles.greyed} key={this.props.mykey}>{this.props.mystring}</Text> 
        </View>
        : donothing() }
    </TouchableOpacity> 

    )   


}};

DrawBox.propTypes = { 
    onPress: PropTypes.func.isRequired, 
    onPressFail: PropTypes.func.isRequired, 
    mykey: PropTypes.number.isRequired, 
    rowstatus: PropTypes.number.isRequired, 
    mydetails: PropTypes.string.isRequired , 
    mycolor: PropTypes.string.isRequired, 
    rowpath: PropTypes.string.isRequired, 

};

