import React, {Component, useState} from 'react';
import {Text, TouchableOpacity, View, StyleSheet, Image} from 'react-native';
import PropTypes from 'prop-types';
import DiceBox from "./dicebox.js" ;
import GLOBAL from "./global.js";

export default class DiceRow extends Component {
    constructor(props) {
        super(props);

        this.state = {
            diceStates: [6,6,6,6,6,6]
          };
           
    }
 
render() { 

    return ( 
      <View style={styles.one_row} >
        <TouchableOpacity style={styles.one_row} onPress={() => {
                if (GLOBAL.GAMESTATE == 'wait for roll' || GLOBAL.GAMESTATE == 'roll or pick color') {
                // roll the dice! 
                let tmparray = []
                for (let i = 0 ; i < 6 ; i++) {
                    tmparray.push(Math.floor(((Math.random()*6)+1)))
                }
                this.setState({diceStates: [...tmparray]}) 
                GLOBAL.DICESTATE=[...tmparray]
                GLOBAL.GAMESTATE = 'wait for first pick';
                GLOBAL.screen1.setState( {statetext: 'Choose white+white or white+color'} )
            //    console.log(GLOBAL.GAMESTATE, ' finished rolling', GLOBAL.DICESTATE)
                }  
            }}>
             
            <DiceBox mykey='red' mycolor='#fd4a4a' myvalue={this.state.diceStates[0]}/>
            <DiceBox mykey='yellow' mycolor='#ffe77c' myvalue={this.state.diceStates[1]}/>
            <DiceBox mykey='green' mycolor='#009c87' myvalue={this.state.diceStates[2]}/>
            <DiceBox mykey='grey' mycolor='#acb0b5' myvalue={this.state.diceStates[3]}/>
            <DiceBox mykey='w1' mycolor='white' myvalue={this.state.diceStates[4]}/>
            <DiceBox mykey='w2' mycolor='white' myvalue={this.state.diceStates[5]}/>
            
           
        </TouchableOpacity>
         </View>
      )
}
};



const styles = StyleSheet.create({
  one_row: {
  //  flex: 1,
    alignItems: 'flex-start',
    justifyContent: 'flex-start',
    flexDirection: 'row',
 //   flexWrap: 'wrap',
    marginTop: 0,
    marginBottom: 0,

    flexShrink: 40,
  },
    stretch: {
      width: 40,
      height: 40,
  },

})
  ;