import React, {Component, useState} from 'react';
import {Text, TouchableOpacity, View, StyleSheet, Image} from 'react-native';
import PropTypes from 'prop-types';
//import images from './imageloader.js'

const images={
"1": require("./assets/1.png"),
"2": require("./assets/2.png"),
"3": require("./assets/3.png"),
"4": require("./assets/4.png"),
"5": require("./assets/5.png"),
"6": require("./assets/6.png")
}

function donothing() {}

export default class DiceBox extends Component {
    constructor(props) {
        super(props);

    }

render() { 

var myImage = images[this.props.myvalue.toString()];
    
    return (   

        <View style={[styles.letterbox_selected, {backgroundColor: this.props.mycolor }]} key={this.props.mykey} >
            <Image style={[styles.stretch, {backgroundColor: this.props.mycolor }]} source={myImage} />
        </View> 

    )   


}};

DiceBox.propTypes = { mykey: PropTypes.string.isRequired, myvalue: PropTypes.number.isRequired , 
     mycolor: PropTypes.string.isRequired, };

const styles = StyleSheet.create({
      letterbox_selected: {
        flex: 0,
        flexBasis: 40,
        backgroundColor: '#fff',
        alignItems: 'center',
        justifyContent: 'center',
        padding: 0,
        margin: 2,
        width: 40,
        height: 40,
        borderWidth: 0,
        flexShrink: 40,
      },
      stretch: {
        width: 40,
        height: 40,
      //  display: 'flex',
      },
    }
)
