import React, {Component} from 'react';
import {Text, View, StyleSheet, SafeAreaView, TouchableOpacity, ScrollView} from 'react-native';
import DrawRow from "./drawrow.js" ;
import DiceRow from "./dicerow.js"
import GLOBAL from "./global.js"


export default class MainApp extends Component {
    constructor(props) {
        super(props);

        this.state = {score: 0, statetext: 'Click dice to roll',gameno: 0, travelins: 1},
        GLOBAL.screen1 = this;
        
    }
 


    render() { 
    

    return(

 
        
        <SafeAreaView key={GLOBAL.screen1.state.gameno} style={styles.outer_container}>
        <ScrollView>
        <View style={styles.container} >
        <DrawRow mykey='africa' rowcolor='#fd4a4a' reverseBool/>
        <DrawRow mykey='americas' rowcolor='#ffe77c' reverseBool/>
        <DrawRow mykey='asia' rowcolor='#009c87' reverseBool={false}/>
        <DrawRow mykey='europe' rowcolor='#acb0b5' reverseBool={false}/>
        <DiceRow  mykey='dicerow'/>
        
        <View style={styles.mystatus}><Text>Your score is: {this.state.score}.  {this.state.statetext}</Text></View>
        <View style={styles.myrows}>
            <TouchableOpacity onPress={() => 
            {
            GLOBAL.screen1.setState({gameno: GLOBAL.screen1.state.gameno+1, score: 0, statetext: 'Restarted! Click dice to roll.', travelins: 1})
            GLOBAL.GAMESTATE = 'wait for roll'}}><View style={styles.mybox}><Text>Restart</Text></View></TouchableOpacity>
            
        {GLOBAL.screen1.state.travelins>0? <TouchableOpacity onPress={() => 
            {GLOBAL.screen1.setState({travelins:0, statetext:"Ready to reroll"}) ; GLOBAL.GAMESTATE = 'wait for roll'}}>
                <View style={styles.mybox}><Text>Insurance</Text></View></TouchableOpacity> : null}
        </View>
        </View>
        </ScrollView>
        </SafeAreaView>





);
}};

const styles = StyleSheet.create({
    outer_container: {
        backgroundColor: '#000',
        alignItems: 'center',
    //    alignContent: 'space-around',
        justifyContent: 'space-around',
        flexWrap: 'wrap',
        flex: 1
    },
    myrows: {
        flex: 1,
        flexDirection: 'row',
  //
        alignItems: 'center',
        alignContent: 'space-around',
        
    },
    container: {
        backgroundColor: '#fff',
        alignItems: 'center',
        alignContent: 'space-between',
        justifyContent: 'space-between',
        flexWrap: 'wrap',
        
},
flex_me: {
    flex: 1,
    flexWrap:'wrap'
},
mybox: {
    backgroundColor: '#fd4a4a',
    borderWidth: 2,
    marginLeft: 20,
    marginRight: 20,
    marginBottom:10,
    padding: 10,

},
mystatus: {

    height: 45,
    marginLeft: 5,
    marginTop: 0,
    marginRight: 5,
    padding: 10,

}
}
);
