import GLOBAL from "./global.js"


export function canUseWhite(index,reverseBool) {
    // passed index, which is from the box clicked.  index+2 is the value of the box clicked
    // grab the white die values and sum
    if (!reverseBool) {
        if (index+2  == GLOBAL.DICESTATE[4] + GLOBAL.DICESTATE[5]) {return true} else {return false}
    } else {
        if (12-index  == GLOBAL.DICESTATE[4] + GLOBAL.DICESTATE[5]) {return true} else {return false}
    }
    
}

export function canUseColor(index,row,reverseBool) {
    if (!reverseBool) {   
        if (row == 'africa' && (GLOBAL.DICESTATE[0] + GLOBAL.DICESTATE[4] == index+2 || 
            GLOBAL.DICESTATE[0] + GLOBAL.DICESTATE[5] == index+2 )) {return true}
        if (row == 'americas' && (GLOBAL.DICESTATE[1] + GLOBAL.DICESTATE[4] == index+2 || 
            GLOBAL.DICESTATE[1] + GLOBAL.DICESTATE[5] == index+2 )) {return true}
        if (row == 'asia' && (GLOBAL.DICESTATE[2] + GLOBAL.DICESTATE[4] == index+2 || 
            GLOBAL.DICESTATE[2] + GLOBAL.DICESTATE[5] == index+2 )) {return true}
        if (row == 'europe' && (GLOBAL.DICESTATE[3] + GLOBAL.DICESTATE[4] == index+2 || 
            GLOBAL.DICESTATE[3] + GLOBAL.DICESTATE[5] == index+2 )) {return true}
        return false
    } else {
        if (row == 'africa' && (GLOBAL.DICESTATE[0] + GLOBAL.DICESTATE[4] == 12-index || 
            GLOBAL.DICESTATE[0] + GLOBAL.DICESTATE[5] == 12-index )) {return true}
        if (row == 'americas' && (GLOBAL.DICESTATE[1] + GLOBAL.DICESTATE[4] == 12-index || 
            GLOBAL.DICESTATE[1] + GLOBAL.DICESTATE[5] == 12-index )) {return true}
        if (row == 'asia' && (GLOBAL.DICESTATE[2] + GLOBAL.DICESTATE[4] == 12-index || 
            GLOBAL.DICESTATE[2] + GLOBAL.DICESTATE[5] == 12-index )) {return true}
        if (row == 'europe' && (GLOBAL.DICESTATE[3] + GLOBAL.DICESTATE[4] == 12-index || 
            GLOBAL.DICESTATE[3] + GLOBAL.DICESTATE[5] == 12-index )) {return true}
        return false      
    }
}